export default {
	data() {
		return {
			pageIndex:1,
			pageSize:10,
			pageTotal:1,
			nomore:false,
			nodata:false,
			productList:[],
			showNum:true,
			banner:getApp().globalData.config.businessPath+'static/ss/mp/images/top_goods.png'
		};
	},
	onShow(){
		this.initData()
	},
	onReachBottom(){
		if (this.pageIndex < this.pageTotal) {
			this.pageIndex++
			this.initData()
		} else {
			this.nomore = true
			return false;
		}
	},
	onPullDownRefresh(){
		this.resetData()
		this.initData()
		uni.stopPullDownRefresh();
	},
	methods:{
		initData(){
			let self = this
			let params = {
				pageSize:this.pageSize,
				pageIndex:this.pageIndex
			}
			this.$acFrame.HttpService.prodRank(params).then(res=>{
				if(res.success){
					let list= res.data.rows
					self.pageTotal = res.data.pageTotal
					self.pageSize = res.data.pageSize
					if(list.length>0){
						self.productList = self.productList.concat(list)
					}else{
						self.nodata = true
					}
				}
			})
		},
		setImg(src){
			return  this.$acFrame.Util.setImgUrl(src);
		},
		resetData(){
			this.pageSize = 10
			this.pageIndex = 1
			this.nodata = false
			this.nomore = false
			this.productList=[]
		}
	}
}
